import { useEffect, useState } from "react";
import { DateTime } from "luxon";

import { T_ClockDataForTimeZone, T_ClocksDataState } from "./types";
import worldCountries from "constants/worldCountries";

export function useClocksData(otherTimeZones?: string[]) {
  const otherTimeZonesOnly = otherTimeZones?.filter(
    (tz) => localTimeZoneName !== tz
  );

  const [state, setState] = useState<T_ClocksDataState>(
    getClocksDataForLocalAndOtherTimeZones(otherTimeZonesOnly)
  );

  useEffect(() => {
    const intervalId = setInterval(() => {
      setState(getClocksDataForLocalAndOtherTimeZones(otherTimeZonesOnly));
    }, 1000);
    return () => clearInterval(intervalId);
    // otherTimeZonesWithoutLocal is computed based on otherTimeZones
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [otherTimeZones]);

  return state;
}

function getClocksDataForLocalAndOtherTimeZones(otherTimeZones?: string[]) {
  const now = DateTime.now();
  const clocksData = [getClockDataForTimeZone(now)];
  if (otherTimeZones) {
    clocksData.push(
      ...otherTimeZones.map((tz) => getClockDataForTimeZone(now, tz))
    );
  }
  return clocksData;
}

function getClockDataForTimeZone(
  datetime: DateTime,
  timezone?: string
): T_ClockDataForTimeZone {
  const nowInTimeZone = timezone ? datetime.setZone(timezone) : datetime;
  const {
    // timeZone
    zoneName,
    offsetNameLong,
    offsetNameShort,
    // time
    hour,
    minute,
    second,
    isInDST,
    // date
    year,
    monthShort,
    monthLong,
    day,
    weekdayLong,
    weekdayShort
  } = nowInTimeZone;

  const timeZoneName = standardizeTimeZoneName(zoneName);
  const meridiem = nowInTimeZone.toFormat("a");
  const countryInfo = worldCountries.find(
    (info) => info.timeZone === timeZoneName
  ) || {
    countryCode: "",
    countryName: ""
  };

  return {
    isLocal: !timezone,
    timeZoneName,
    offsetNameLong,
    offsetNameShort,
    ...countryInfo,
    hour: nowInTimeZone.toFormat("hh"),
    minute: nowInTimeZone.toFormat("mm"),
    second: nowInTimeZone.toFormat("ss"),
    meridiem,
    isInDST,
    year,
    monthShort,
    monthLong,
    day,
    weekdayLong,
    weekdayShort,
    hourDeg: (hour / 12) * 360 + (minute / 60) * 30 + 90,
    minuteDeg: (minute / 60) * 360 + (second / 60) * 6 + 90,
    secondDeg: (second / 60) * 360 + 90
  };
}

const localTimeZoneName = standardizeTimeZoneName(DateTime.now().zoneName);

function standardizeTimeZoneName(timeZoneName: string) {
  return timeZoneName.replace(
    // TODO: Stupid Chrome https://bugs.chromium.org/p/chromium/issues/detail?id=580195
    "Calcutta",
    "Kolkata"
  );
}
