import Fuse from "fuse.js";

import worldCountries from "constants/worldCountries";

// @see https://fusejs.io/api/options.html
const fuzzySearchOptions = {
  // isCaseSensitive: false,
  // includeScore: false,
  // shouldSort: true,
  // includeMatches: false,
  // findAllMatches: false,
  // minMatchCharLength: 1,
  // location: 0,
  // threshold: 0.6,
  // distance: 100,
  // useExtendedSearch: false,
  // ignoreLocation: false,
  // ignoreFieldNorm: false,
  // fieldNormWeight: 1,
  keys: ["countryName", { name: "timeZone", weight: 2 }, "countryCode"]
};

// @see https://fusejs.io/api/indexing.html
const siteIndex = Fuse.createIndex(fuzzySearchOptions.keys, worldCountries);

// initialize Fuse with the index
export const fuzzySearch = new Fuse(
  worldCountries,
  fuzzySearchOptions,
  siteIndex
);
